import React, { Component } from 'react';

export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div>
        <table style={ style.table }>
          <tr>
            <td style={ style.cellNarrow }>Task:</td>
            <td><input type='text' style={ style.textInput } /></td>
            <td><input type='checkbox' style={ style.cbox } /><div style={ style.name }>New Task</div></td>
            <td><input type='checkbox' style={ style.cbox }/><div style={ style.name }>On-Going Task</div></td>
          </tr>
          <tr>
            <td style={ style.cellNarrow }>Project:</td>
            <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            <td style={ style.cellNarrow }>Start Date:</td>
            <td style={ style.cellWide }><input type='date' style={ style.textInput } /></td>
          </tr>
          <tr>
            <td style={ style.cellNarrow }>Contractor:</td>
            <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            <td style={ style.cellNarrow }>End Date:</td>
            <td style={ style.cellWide }><input type='date' style={ style.textInput } /></td>
          </tr>
          <tr>
            <td style={ style.cellNarrow }>Supervisor Review:</td>
            <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            <td style={ style.cellNarrow }>Review Date:</td>
            <td style={ style.cellWide }><input type='date' style={ style.textInput } /></td>
          </tr>
          <tr>
            <td style={ style.cellNarrow }>ACCO Review:</td>
            <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            <td style={ style.cellNarrow }>ACCO EHS Review:</td>
            <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
          </tr>
        </table>
      </div>
    );
  }
}

const style = {
  table: {
    marginTop: '0.20in',
    marginLeft: '0.20in',
    marginRight: '0.20in',
    marginBottom: '0.025in'
  },
  cellNarrow: {
    width: '1.25in',
    height: '0.1875in',
    fontSize: '8pt',
    paddingLeft: '5px'
  },
  cellWide: {
    width: '2.75in',
    height: '0.125in',
    fontSize: '8pt',
    padding: '0px'
  },
  cbox: {
    position: 'relative',
    float: 'left'
  },
  name: {
    fontSize: '8pt',
    position: 'relative',
    float: 'left',
    paddingTop: '2.5px'
  },
  textInput: {
    width: '2.625in',
    // height: '0.25in',
    border: '0',
    paddingLeft: '0.125in'
  }
};
