import ReactDOM from 'react-dom';
import React, { Component } from 'react';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';
import ViewTable from './components/ViewTable';
import Header from './components/Header';
import Footer from './components/Footer';
import { items, inputs } from './data/checkBoxData';
import activations from './data/activations';
import Immutable from 'immutable';

export default class App extends Component {
  constructor(props) {
    super(props);
    // this.state = Immutable.fromJS({
    //   currentHighlights: {
    //     'Q5': ['L7']
    //   }
    // });

    this.state = {
      root: Immutable.fromJS({
        currentHighlights: {
          'Q5': ['L7']
        }
      })
    };

    this.onCheck = this.onCheck.bind(this);
    this.printDocument = this.printDocument.bind(this);
  }

  printDocument() {
    const input = document.getElementById('divToPrint');
    html2canvas(input)
      .then((canvas) => {
        const imgData = canvas.toDataURL('image/png');
        const pdf = new jsPDF();
        pdf.addImage(imgData, 'JPEG', 0, 0);
        pdf.save('download.pdf');
      });
  }

  onCheck(id, status) {
    const state = this.state.root;
    if(activations[id] === undefined) return;

    switch(status) {
      case true: {
        const cH = state.setIn(['currentHighlights', id], activations[id]);
        this.setState({ root: cH });
        break;
      }
      case false: {
        const cH = state.deleteIn(['currentHighlights', id]);
        this.setState({ root: cH });
        break;
      }
    }
  }

  render() {
    const hazards = items.filter(e => e.type === 'hazards');
    const ppe = items.filter(e => e.type === 'ppe');
    const safeguards = items.filter(e => e.type === 'safeguards');

    const currentHighlights = this.state.root.get('currentHighlights').toJS();

    return(
      <div style={ style.printStyleUS }>
        <div style={ style.header }>
          <img src='https://s3-us-west-2.amazonaws.com/s3arch-dev/logo.png' style={{ float: 'left'} }/>
          <div style={{ textAlign: 'center', fontWeight: '700', paddingTop: '10px' }}>Pre Task Plan Worksheet</div>
        </div>
        <Header />
        <div style={ style.title }>IDENTIFY THE TASK HAZARD EXPOSURES</div>
        <div style={{ marginBottom: '0.125in' }}><ViewTable columns={ 4 } elements={ inputs } currentHighlights={ currentHighlights } onCheck={ this.onCheck } header={ exposureHeader } /></div>
        <div style={{ marginBottom: '0.025in' }}><ViewTable columns={ 4 } elements={ hazards } currentHighlights={ currentHighlights } onCheck={ this.onCheck } header={ hazardHeader } /></div>
        <div style={ style.title }>IDENTIFY SAFEGUARDS</div>
        <div style={{ marginBottom: '0.125in' }}><ViewTable columns={ 4 } elements={ ppe } currentHighlights={ currentHighlights } onCheck={ this.onCheck } header={ ppeHeader } /></div>
        <div style={{ marginBottom: '0.025in' }}><ViewTable columns={ 4 } elements={ safeguards } currentHighlights={ currentHighlights } onCheck={ this.onCheck } header={ safeHeader } /></div>
        <div style={ style.title }>ADDITIONAL REQUIRED DOCUMENTATION AND EMERGENCY RESPONSE INFORMATION</div>
        <Footer />
      </div>
    );
  }
}

{/* <div className='mb5'>
  <button onClick={ this.printDocument }>Print</button>
</div>
<div id='divToPrint' className='mt4' style={ style.printStyle }>
  <div>Note: Here the dimensions of div are the same as A4</div>
  <div>You can add any component here</div>
</div> */}

const exposureHeader = 'IDENTIFY WORK EXPOSURES (WHAT CAN HURT ME?)';
const hazardHeader = 'IDENTIFY SPECIFIC HAZARDS ASSOCIATED WITH THE WORK EXPOSURES (HOW CAN I GET HURT?)';
const ppeHeader = 'PERSONAL PROTECTIVE EQUIPMENT THAT WILL BE USED (WHAT WILL KEEP ME SAFE?)';
const safeHeader = 'IDENTIFY TASK SPECIFIC SAFE GUARDS (HOW DO I STAY SAFE?)';

const style = {
  header: {
    marginLeft: '0.2in',
    marginTop: '0.2in'
  },
  title: {
    width: '100%',
    textAlign: 'center',
    fontWeight: '700'
  },
  printStyleMetric: {
    // backgroundColor: '#f5f5f5',
    width: '210mm',
    minHeight: '297mm',
    marginLeft: 'auto',
    marginRight: 'auto'
  },
  printStyleUS: {
    // backgroundColor: '#f5f5f5',
    width: '8.5in',
    minHeight: '11in',
    marginLeft: 'auto',
    marginRight: 'auto'
  }
}

ReactDOM.render(<App />, document.querySelector('#main'));
