import React, { Component } from 'react';

export default class TableElement extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { elem, highlight, onCheck } = this.props;
    if(elem === undefined) {
      return <td id='none'></td>
    }

    const { cell, name, type } = elem;
    const cellStyle = {...style.cell, background: highlight ? 'yellow' : 'none' };
    return (
      <td id={ cell } style={ cellStyle }>
        <input type='checkbox' id={ cell + '_chk' } style={ style.cbox } onClick={ ({target}) => onCheck(cell, target.checked) } />
        <div style={ style.name }>{ name }</div>
      </td>
    );
  }
}

const style = {
  cell: {
    width: '2.225in',
    height: '0.15in',
    fontSize: '8pt'
  },
  cbox: {
    position: 'relative',
    // height: '0.15in',
    float: 'left',
    marginTop: '2px',
    marginBottom: '2px'
  },
  name: {
    position: 'relative',
    // height: '0.15in',
    float: 'left',
    paddingTop: '2.5px'
  }
};
