import React, { Component } from 'react';

export default class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div>
        <table style={ style.table }>
          <tbody>
            <tr><th colSpan='4' style={ style.theader }>PERMITS / PLANS REQUIRED FOR THE TASK</th></tr>
            <tr><th colSpan='4' style={ style.theader }><input type='text' style={ {...style.textInput, width: '8in', height: '0.25in'} } /></th></tr>
            <tr><th colSpan='4' style={ style.theader }>EMERGENCY RESPONSE INFORMATION</th></tr>
            <tr>
              <td style={ style.cellNarrow }>Nearest Phone:</td>
              <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
              <td style={ style.cellNarrow }>Nearest F.E.:</td>
              <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            </tr>
            <tr>
              <td style={ style.cellNarrow }>Nearest Exit:</td>
              <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
              <td style={ style.cellNarrow }>Nearest Eyewash:</td>
              <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            </tr>
            <tr>
              <td style={ style.cellNarrow }>Assembly Area:</td>
              <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
              <td style={ style.cellNarrow }>Nearest First Aid Kit:</td>
              <td style={ style.cellWide }><input type='text' style={ style.textInput } /></td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

const style = {
  theader: {
    border: '1px solid black',
    background: 'rgb(200,200,200)',
    color: 'rgb(220,0,0)',
    fontSize: '9pt',
    fontWeight: '700'
  },
  table: {
    // marginTop: '0.20in',
    marginLeft: '0.20in',
    marginRight: '0.20in',
    marginBottom: '0.025in'
  },
  cellNarrow: {
    width: '1.25in',
    height: '0.1875in',
    fontSize: '8pt',
    paddingLeft: '5px'
  },
  cellWide: {
    width: '2.75in',
    height: '0.125in',
    fontSize: '8pt',
    padding: '0px'
  },
  cbox: {
    position: 'relative',
    float: 'left'
  },
  name: {
    fontSize: '8pt',
    position: 'relative',
    float: 'left',
    paddingTop: '2.5px'
  },
  textInput: {
    width: '2.625in',
    // height: '0.25in',
    border: '0',
    paddingLeft: '0.125in'
  }
};
