import React, { Component } from 'react';
import TableElement from './TableElement';

export default class ViewTable extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.makeRows = this.makeRows.bind(this);
    this.checkHighlight = this.checkHighlight.bind(this);
  }

  checkHighlight(currentHighlights, toCheck) {
    let status = false;
    Object.keys(currentHighlights).map(key => {
      const containCheck = currentHighlights[key].indexOf(toCheck);
      if(containCheck !== -1) {
        status = true;
      }
    });
    return status;
  }

  makeRows(elements) {
    const { currentHighlights, columns, onCheck } = this.props;

    const numRows = Math.ceil(elements.length / columns);
    const rows = [];
    let row = [];
    for (var i = 1; i < elements.length + 1; i++) {
      const highlight = this.checkHighlight(currentHighlights, elements[i - 1].cell);
      row.push(<TableElement elem={ elements[i - 1] } highlight={ highlight } onCheck={ onCheck } />);
      if( i % columns === 0) {
        rows.push(<tr>{ row }</tr>);
        row = [];
      }
    }

    if(rows.length < numRows) {
      while (row.length < columns) {
        row.push(<TableElement />);
      }
      rows.push(<tr>{ row }</tr>);
    }
    return rows;
  }

  render() {
    const { elements } = this.props;
    const rows = this.makeRows(elements);

    return (
      <table style={ style.table }>
        <tbody>
          <tr>
            <th colspan={ this.props.columns } style={ style.header }>{ this.props.header }</th>
          </tr>
          { rows }
        </tbody>
      </table>
    );
  }
}

const style = {
  header: {
    background: 'rgb(200,200,200)',
    color: 'rgb(220,0,0)',
    fontSize: '9pt',
    fontWeight: '700'
  },
  table: {
    marginLeft: '0.20in',
    marginRight: '0.20in',
    // marginBottom: '0.125in'
  }
};
